import { Options, Vue } from "vue-class-component";
import { memo } from "@/types/memo/memo";
import { httpClient } from "@/../module/axios";

@Options({
  components: {
    //HelloWorld,
  },
})
export class MemoDetail extends Vue {
  private memo: memo = {
    id: 0,
    title: "",
    content: "",
  };

  public async created(): Promise<void> {
    const id = this.$route.params.id;
    const resp = await httpClient.get(`memo/${id}`);
    this.memo = resp.data.memo;
  }

  // メモ更新 Todo Rest側未実装 引数id: number
  private onClickUpdate(): void {
    console.warn("onClickUpdate");
    this.$router.go(-1);
  }

  // メモ削除
  private async onClickDelete(id: number): Promise<void> {
    const resp = await httpClient.delete(`memo/${id}`);
    if (resp.status === 204) {
      alert("削除しました。");
      this.$router.go(-1);
    } else {
      console.warn(resp.status);
      alert("削除に失敗しました");
    }
  }
}
