import { Options, Vue } from "vue-class-component";
import { memo } from "@/types/memo/memo";
import { httpClient } from "@/../module/axios";

@Options({
  components: {},
})
export class MemoCreate extends Vue {
  private hhh = "fjgjgdi";
  private memo: memo = {
    id: 0,
    title: "",
    content: "",
  };

  public created(): void {
    // const id = this.$route.params.id;
    // const resp = await axios.get(`/api/memo/${id}`);
    // this.memo = resp.data.memo;
  }

  private onClickBackPage(): void {
    this.$router.go(-1);
  }

  private async onClickCreate(): Promise<void> {
    const title = (this.$refs.memo_title as HTMLInputElement).value;
    const content = (this.$refs.memo_content as HTMLInputElement).value;

    if (!title || !content) {
      alert("フォームに入力してください");
      return;
    }
    const memo: memo = {
      title: title,
      content: content,
    };
    const config = { headers: { ["Content-Type"]: "application/json" } };
    const resp = await httpClient.post("memo/", memo, config);
    console.warn(resp);
    alert("メモを作成しました");
    this.$router.go(-1);
  }
}
